import { Plugin } from '@ali/4ever-cangjie';
import { toggleFold } from '@ali/4ever-plugin-group-factory';
import { createAutoUnfold } from '@ali/4ever-plugin-heading-group';

function createOnCangjieSelect(config: Record<string, any>): Plugin['onCangjieSelect'] {
  const { autoUnfold = false, fold = false } = config;

  const handleAutoUnfold = createAutoUnfold({
    onAutoUnfold: (controller, node) => controller.run('onAction', toggleFold({
      node,
      fold,
    })),
  });

  return function onCangjieSelect(event, controller, next) {
    if (autoUnfold) {
      handleAutoUnfold(event, controller);
    }
    return next();
  };
}

export default createOnCangjieSelect;
